import { Button, Col, Form } from "@themesberg/react-bootstrap";
import React, { useContext, useState } from "react";
import toast, { Toaster } from "react-hot-toast";
import { AuthContext } from "../../../context/AuthContext";
import bglogin from "../../../assets/img/register_bg_2.png";
import { Link, Redirect } from "react-router-dom";
import "../../../css/RegisterForm.css";
import { faInfoCircle } from "@fortawesome/free-solid-svg-icons";
import { inputFields, newRegisterFormData } from "../../../constants";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";

const StudentRegister = () => {
    const { state, new_register } = useContext(AuthContext);
    const { isAuthenticated } = state;

    const [formData, setFormData] = useState(newRegisterFormData);

    const [loading, setLoading] = useState(false);

    const handleInputChange = (e) => {
        const { name, value } = e.target;

        if (name === "roll_no" || name === "blood_group") {
            setFormData({
                ...formData,
                [name]: value.toUpperCase()
            })
            return;
        }

        setFormData({
            ...formData,
            [name]: value
        })

    }

    const [response, setResponse] = useState(null);

    const handleSubmit = async (e) => {
        e.preventDefault();

        try {
            setLoading(true);
            setResponse(null);
            const res = await new_register(formData);
            if (res.data.success) {
                toast.success("Data saved successfully for verification. You will receive a mail once your data is verified.");
                setResponse("Data saved successfully for verification. You will receive a mail once your data is verified.");
                setFormData(newRegisterFormData);
            }
            else {
                toast.error(res.data.message);
                setResponse(res.data.message);
            }
            setLoading(false);
        } catch (error) {
            console.error('Error:', error.response ? error.response.data : error.message);
            toast.error("Some error occured");
            setLoading(false);
            setResponse('Error');
        }
    };

    if (isAuthenticated) {
        return <Redirect to="/" />;
    }

    return (
        <div style={{ background: "#262b40" }}>
            <Toaster />
            <div
                className="d-flex justify-content-center registerContainer px-4 align-items-center"
                style={{
                    backgroundImage: `url(${bglogin})`,
                    width: "100%",
                    backgroundSize: "cover",
                    background: "fixed",
                    overflow: "scroll",
                }}
            >
                <div style={{ overflow: "scroll", maxWidth: "100%", width: "100%", color: "orange" }} className="registerForm">
                    <div className="d-flex justify-items-center gap-2 mb-2" style={{ width: '100%', fontWeight: '600', fontSize: '18px' }} >
                        <span><FontAwesomeIcon icon={faInfoCircle} /></span>
                        <div>
                            Please ensure you input your information accurately into the form. Verify all details prior to submission, as you have a single opportunity to complete this form.
                        </div>
                    </div>
                    <div className="d-flex justify-items-center gap-2 mb-5" style={{ width: '100%', fontWeight: '600', fontSize: '18px' }} >
                        <span><FontAwesomeIcon icon={faInfoCircle} /></span>
                        <div>
                            Incase of any query: <a href="mailto:placementsin@nsut.ac.in" style={{color: 'blue'}}>placementsin@nsut.ac.in</a>
                        </div>
                    </div>
                    <h2 style={{ color: "darkorange", textAlign: "center" }}>Register</h2>
                    <Form onSubmit={handleSubmit} className="form w-full">
                        {inputFields.map((input, index) => {
                            if (input.type === 'text' || input.type === 'email' || input.type === 'number') {
                                return <Col xs={5} className="mt-3 registerInput" key={input.label + index + input.type} >
                                    <Form.Group>
                                        <Form.Label>{input.label}</Form.Label>
                                        <Form.Control
                                            type={input.type}
                                            required
                                            name={input.name}
                                            value={formData[input.name]}
                                            onChange={handleInputChange}
                                            {...input}
                                        />
                                    </Form.Group>
                                </Col>
                            }
                            else if (input.type === "select") {
                                return <Form.Group className="mt-3" key={input.label + index + input.type}>
                                    <Form.Label>{input.label}</Form.Label>
                                    <Form.Select name={input.name} onChange={handleInputChange} required>
                                        {input.options.map((option, index) => <option key={option.name + index} value={option.value}>{option.name}</option>)}
                                    </Form.Select>
                                </Form.Group>
                            }
                            else if (input.type === "date") {
                                return <Col xs={5} className="mt-3 registerInput" key={input.label + index + input.type} >
                                    <Form.Group>
                                        <Form.Label>{input.label}</Form.Label>
                                        <Form.Control
                                            type={input.type}
                                            required
                                            name={input.name}
                                            value={formData[input.name]}
                                            onChange={handleInputChange}
                                            {...input}
                                        />
                                    </Form.Group>
                                </Col>
                            }
                            else return null;
                        })}
                        <div className="mt-5 registerButton">
                            <Button
                                style={{
                                    width: "100%",
                                    background: "linear-gradient(to right, #fb7b4a, #ee3344)",
                                }}
                                disabled={loading}
                                type="submit"
                            >
                                Register
                            </Button>
                            {response && <div className="mt-1" style={{ color: response.includes('successfully') ? 'green' : 'red', width: '100%', textAlign: 'center', fontWeight: 'bold', fontSize: '20px' }}>
                                {response}
                            </div>}
                        </div>
                        <div className="text-center w-100">
                            or
                        </div>
                        <div className="mt-2 p-2 text-white text-center registerButton" style={{
                            borderRadius: "8px",
                            background: "linear-gradient(to right, #fb7b4a, #ee3344)",
                        }}>
                            <Link
                                to={'/login'}
                                className="text-white text-center"
                                style={{
                                    width: "100%",
                                    display: 'block'
                                }}>
                                Login
                            </Link>
                        </div>
                    </Form>
                </div>
            </div>
        </div>
    );
};
export default StudentRegister;
